import {Size} from './Size';
import {Pixel} from './Pixel';
import {LonLat} from './LonLat';
import {Util} from './Util';

/**
 * @class Bounds
 * @deprecatedclass SuperMap.Bounds
 * @category BaseTypes Geometry
 * @classdesc  bounds  leftbottomrighttop  null
 * @param {number|Array.<number>} [left] - number right  [left, bottom, right, top] 
 * @param {number} [bottom] -  top 
 * @param {number} [right] - 
 * @param {number} [top] - 
 * @example
 * var bounds = new Bounds();
 * bounds.extend(new LonLat(4,5));
 * bounds.extend(new LonLat(5,6));
 * bounds.toBBOX(); // returns 4,5,5,6
 * @usage
 */
export class Bounds {


    constructor(left, bottom, right, top) {
        if (Util.isArray(left)) {
            top = left[3];
            right = left[2];
            bottom = left[1];
            left = left[0];
        }
        /**
         * @member {number} Bounds.prototype.left
         * @description 
         */
        this.left = left != null ? Util.toFloat(left) : this.left;

        /**
         * @member {number} Bounds.prototype.bottom
         * @description 
         */
        this.bottom = bottom != null ? Util.toFloat(bottom) : this.bottom;

        /**
         * @member {number} Bounds.prototype.right
         * @description 
         */
        this.right = right != null ? Util.toFloat(right) : this.right;

        /**
         * @member {number} Bounds.prototype.top
         * @description 
         */
        this.top = top != null ? Util.toFloat(top) : this.top;

        /**
         * @member {LonLat} Bounds.prototype.centerLonLat
         * @description bounds  getCenterLonLat() 
         */
        this.centerLonLat = null;
        this.CLASS_NAME = "SuperMap.Bounds";
    }

    /**
     * @function Bounds.prototype.clone
     * @description  bounds 
     * @example
     * var bounds1 = new Bounds(-180,-90,180,90);
     * var bounds2 = bounds1.clone();
     * @returns {Bounds}  bounds
     */
    clone() {
        return new Bounds(this.left, this.bottom,
            this.right, this.top);
    }

    /**
     * @function Bounds.prototype.equals
     * @description  bounds 
     * @example
     * var bounds1 = new Bounds(-180,-90,180,90);
     * var bounds2 = new Bounds(-180,-90,180,90);
     * var isEquals = bounds1.equals(bounds2);
     * @param {Bounds} bounds -  bounds
     * @returns {boolean}  bounds  bounds  true bounds  NULL  false
     */
    equals(bounds) {
        var equals = false;
        if (bounds != null) {
            equals = ((this.left === bounds.left) &&
                (this.right === bounds.right) &&
                (this.top === bounds.top) &&
                (this.bottom === bounds.bottom));
        }
        return equals;
    }

    /**
     * @function Bounds.prototype.toString
     * @description 
     * @example
     * var bounds = new Bounds(-180,-90,180,90);
     * var str = bounds.toString();
     * @returns {string} left,bottom,right,top: "-180,-90,180,90"
     */
    toString() {
        return [this.left, this.bottom, this.right, this.top].join(",");
    }

    /**
     * @function Bounds.prototype.toArray
     * @description 
     * @example
     * var bounds = new Bounds(-180,-90,100,80);
     * //array1 = [-180,-90,100,80];
     * var array1 = bounds.toArray();
     * //array1 = [-90,-180,80,100];
     * var array2 = bounds.toArray(true);
     * @param {boolean} [reverseAxisOrder=false] - 
     *  truebottom,left,top,rightleft,bottom,right,top
     * @returns {Array.<number>} left, bottom, right, top 
     */
    toArray(reverseAxisOrder) {
        if (reverseAxisOrder === true) {
            return [this.bottom, this.left, this.top, this.right];
        } else {
            return [this.left, this.bottom, this.right, this.top];
        }
    }

    /**
     * @function Bounds.prototype.toBBOX
     * @description  decimal  BBOX 
     * @example
     * var bounds = new Bounds(-1.1234567,-1.7654321,1.4444444,1.5555555);
     * //str1 = "-1.123457,-1.765432,1.444444,1.555556";
     * var str1 = bounds.toBBOX();
     * //str2 = "-1.1,-1.8,1.4,1.6";
     * var str2 = bounds.toBBOX(1);
     * //str2 = "-1.8,-1.1,1.6,1.4";
     * var str2 = bounds.toBBOX(1,true);
     * @param {number} [decimal=6] - 
     * @param {boolean} [reverseAxisOrder=false] - 
     * truebottom,left,top,right,left,bottom,right,top
     * @returns {string} "5,42,10,45"
     */
    toBBOX(decimal, reverseAxisOrder) {
        if (decimal == null) {
            decimal = 6;
        }
        var mult = Math.pow(10, decimal);
        var xmin = Math.round(this.left * mult) / mult;
        var ymin = Math.round(this.bottom * mult) / mult;
        var xmax = Math.round(this.right * mult) / mult;
        var ymax = Math.round(this.top * mult) / mult;
        if (reverseAxisOrder === true) {
            return ymin + "," + xmin + "," + ymax + "," + xmax;
        } else {
            return xmin + "," + ymin + "," + xmax + "," + ymax;
        }
    }

    ///**
    // * @function Bounds.prototype.toGeometry
    // * @description 
    // * @example
    // * var bounds = new Bounds(-180,-90,100,80);
    // * // Polygon
    // * var geo = bounds.toGeometry();
    // * @returns {GeometryPolygon}  bounds 
    // */
    // toGeometry() {
    //     return new Polygon([
    //         new LinearRing([
    //             new Point(this.left, this.bottom),
    //             new Point(this.right, this.bottom),
    //             new Point(this.right, this.top),
    //             new Point(this.left, this.top)
    //         ])
    //     ]);
    // }

    /**
     * @function Bounds.prototype.getWidth
     * @description  bounds 
     * @example
     * var bounds = new Bounds(-180,-90,100,80);
     * //width = 280;
     * var width = bounds.getWidth();
     * @returns {number}  bounds right  left
     */
    getWidth() {
        return (this.right - this.left);
    }

    /**
     * @function Bounds.prototype.getHeight
     * @description  bounds 
     * @example
     * var bounds = new Bounds(-180,-90,100,80);
     * //height = 170;
     * var height = bounds.getHeight();
     * @returns {number} top  bottom
     */
    getHeight() {
        return (this.top - this.bottom);
    }

    /**
     * @function Bounds.prototype.getSize
     * @description 
     * @example
     * var bounds = new Bounds(-180,-90,100,80);
     * var size = bounds.getSize();
     * @returns {Size} 
     */
    getSize() {
        return new Size(this.getWidth(), this.getHeight());
    }

    /**
     * @function Bounds.prototype.getCenterPixel
     * @description 
     * @example
     * var bounds = new Bounds(-180,-90,100,80);
     * var pixel = bounds.getCenterPixel();
     * @returns {Pixel} 
     */
    getCenterPixel() {
        return new Pixel((this.left + this.right) / 2,
            (this.bottom + this.top) / 2);
    }

    /**
     * @function Bounds.prototype.getCenterLonLat
     * @description 
     * @example
     * var bounds = new Bounds(-180,-90,100,80);
     * var lonlat = bounds.getCenterLonLat();
     * @returns {LonLat} 
     */
    getCenterLonLat() {
        if (!this.centerLonLat) {
            this.centerLonLat = new LonLat(
                (this.left + this.right) / 2, (this.bottom + this.top) / 2
            );
        }
        return this.centerLonLat;
    }

    /**
     * @function Bounds.prototype.scale
     * @description / bounds
     * @example
     * var bounds = new Bounds(-50,-50,40,40);
     * var bounds2 = bounds.scale(2);
     * @param {number} [ratio=1] - 
     * @param {(Pixel|LonLat)} [origin] -  bounds 
     * @returns {Bounds}  ratioorigin 
     */
    scale(ratio, origin) {
        ratio = ratio ? ratio : 1;
        if (origin == null) {
            origin = this.getCenterLonLat();
        }

        var origx, origy;

        // get origin coordinates
        if (origin.CLASS_NAME === "SuperMap.LonLat") {
            origx = origin.lon;
            origy = origin.lat;
        } else {
            origx = origin.x;
            origy = origin.y;
        }

        var left = (this.left - origx) * ratio + origx;
        var bottom = (this.bottom - origy) * ratio + origy;
        var right = (this.right - origx) * ratio + origx;
        var top = (this.top - origy) * ratio + origy;

        return new Bounds(left, bottom, right, top);
    }

    /**
     * @function Bounds.prototype.add
     * @description  Bounds 
     * @example
     * var bounds1 = new Bounds(-50,-50,40,40);
     * //bounds2  bounds
     * var bounds2 = bounds.add(20,10);
     * @param {number} x -  x 
     * @param {number} y -  y 
     * @returns {Bounds}  bounds bounds  xy  bounds 
     */
    add(x, y) {
        if ((x == null) || (y == null)) {
            throw new TypeError('Bounds.add cannot receive null values');
        }
        return new Bounds(this.left + x, this.bottom + y,
            this.right + x, this.top + y);
    }

    /**
     * @function Bounds.prototype.extend
     * @description  bounds  bounds pointlanlat  bounds bounds 
     * @example
     * var bounds1 = new Bounds(-50,-50,40,40);
     * //bounds 
     * bounds.extend(new LonLat(50,60));
     * @param {GeometryPoint|LonLat|Bounds} object -  pointlonlat  bounds
     */
    extend(object) {
        var bounds = null;
        if (object) {
            // clear cached center location
            switch (object.CLASS_NAME) {
                case "SuperMap.LonLat":
                    bounds = new Bounds(object.lon, object.lat,
                        object.lon, object.lat);
                    break;
                case "SuperMap.Geometry.Point":
                    bounds = new Bounds(object.x, object.y,
                        object.x, object.y);
                    break;

                case "SuperMap.Bounds":
                    bounds = object;
                    break;
            }

            if (bounds) {
                this.centerLonLat = null;
                if ((this.left == null) || (bounds.left < this.left)) {
                    this.left = bounds.left;
                }
                if ((this.bottom == null) || (bounds.bottom < this.bottom)) {
                    this.bottom = bounds.bottom;
                }
                if ((this.right == null) || (bounds.right > this.right)) {
                    this.right = bounds.right;
                }
                if ((this.top == null) || (bounds.top > this.top)) {
                    this.top = bounds.top;
                }
            }
        }
    }

    /**
     * @function Bounds.prototype.containsLonLat
     * @description 
     * @example
     * var bounds1 = new Bounds(-50,-50,40,40);
     * //isContains1 = true
     * // boolean inclusive
     * var isContains1 = bounds.containsLonLat(new LonLat(40,40),true);
     *
     * //(40,40)(40+360,40)
     * var bounds2 = new Bounds(-50,-50,40,40);
     * //isContains2 = true;
     * var isContains2 = bounds2.containsLonLat(
     *      new LonLat(400,40),
     *      {
     *           inclusive:true,
     *           //
     *           worldBounds: new Bounds(-180,-90,180,90)
     *      }
     * );
     * @param {(LonLat|Object)} ll - <LonLat>  'lon'  'lat' 
     * @param {Object} options - 
     * @param {boolean} [options.inclusive=true] - 
     * @param {Bounds} [options.worldBounds] -  worldBounds ,  ll worldBounds
     *        , 
     * @returns {boolean} 
     */
    containsLonLat(ll, options) {
        if (typeof options === "boolean") {
            options = {inclusive: options};
        }
        options = options || {};
        var contains = this.contains(ll.lon, ll.lat, options.inclusive),
            worldBounds = options.worldBounds;
        //
        if (worldBounds && !contains) {
            var worldWidth = worldBounds.getWidth();
            var worldCenterX = (worldBounds.left + worldBounds.right) / 2;
            //
            var worldsAway = Math.round((ll.lon - worldCenterX) / worldWidth);
            contains = this.containsLonLat({
                lon: ll.lon - worldsAway * worldWidth,
                lat: ll.lat
            }, {inclusive: options.inclusive});
        }
        return contains;
    }

    /**
     * @function Bounds.prototype.containsPixel
     * @description 
     * @example
     * var bounds = new Bounds(-50,-50,40,40);
     * //isContains = true
     * var isContains = bounds.containsPixel(new Pixel(40,40),true);
     * @param {Pixel} px - 
     * @param {boolean} [inclusive=true] - 
     * @returns {boolean}  pixel 
     */
    containsPixel(px, inclusive) {
        return this.contains(px.x, px.y, inclusive);
    }

    /**
     * @function Bounds.prototype.contains
     * @description  xy 
     * @example
     * var bounds = new Bounds(-50,-50,40,40);
     * //isContains = true
     * var isContains = bounds.contains(40,40,true);
     * @param {number} x - x 
     * @param {number} y - y 
     * @param {boolean} [inclusive=true] - 
     * @returns {boolean}  xy 
     */
    contains(x, y, inclusive) {
        //set default
        if (inclusive == null) {
            inclusive = true;
        }

        if (x == null || y == null) {
            return false;
        }

        //x = Util.toFloat(x);
        //y = Util.toFloat(y);

        var contains = false;
        if (inclusive) {
            contains = ((x >= this.left) && (x <= this.right) &&
                (y >= this.bottom) && (y <= this.top));
        } else {
            contains = ((x > this.left) && (x < this.right) &&
                (y > this.bottom) && (y < this.top));
        }
        return contains;
    }

    /**
     * @function Bounds.prototype.intersectsBounds
     * @description 
     *              
     * @example
     * var bounds = new Bounds(-180,-90,100,80);
     * var isIntersects = bounds.intersectsBounds(
     *      new Bounds(-170,-90,120,80)
     *  );
     * @param {Bounds} bounds - 
     * @param {Object} options - 
     * @param {boolean} [options.inclusive=true] - false
     *                               
     *                               
     * @param {Bounds} [options.worldBounds] -  worldBounds , 
     *                               , 
     * @returns {boolean}  bounds  bounds 
     */
    intersectsBounds(bounds, options) {
        if (typeof options === "boolean") {
            options = {inclusive: options};
        }
        options = options || {};
        if (options.worldBounds) {
            var self = this.wrapDateLine(options.worldBounds);
            bounds = bounds.wrapDateLine(options.worldBounds);
        } else {
            self = this;
        }
        if (options.inclusive == null) {
            options.inclusive = true;
        }
        var intersects = false;
        var mightTouch = (
            self.left === bounds.right ||
            self.right === bounds.left ||
            self.top === bounds.bottom ||
            self.bottom === bounds.top
        );

        // if the two bounds only touch at an edge, and inclusive is false,
        // then the bounds don't *really* intersect.
        if (options.inclusive || !mightTouch) {
            // otherwise, if one of the boundaries even partially contains another,
            // inclusive of the edges, then they do intersect.
            var inBottom = (
                ((bounds.bottom >= self.bottom) && (bounds.bottom <= self.top)) ||
                ((self.bottom >= bounds.bottom) && (self.bottom <= bounds.top))
            );
            var inTop = (
                ((bounds.top >= self.bottom) && (bounds.top <= self.top)) ||
                ((self.top > bounds.bottom) && (self.top < bounds.top))
            );
            var inLeft = (
                ((bounds.left >= self.left) && (bounds.left <= self.right)) ||
                ((self.left >= bounds.left) && (self.left <= bounds.right))
            );
            var inRight = (
                ((bounds.right >= self.left) && (bounds.right <= self.right)) ||
                ((self.right >= bounds.left) && (self.right <= bounds.right))
            );
            intersects = ((inBottom || inTop) && (inLeft || inRight));
        }
        // document me
        if (options.worldBounds && !intersects) {
            var world = options.worldBounds;
            var width = world.getWidth();
            var selfCrosses = !world.containsBounds(self);
            var boundsCrosses = !world.containsBounds(bounds);
            if (selfCrosses && !boundsCrosses) {
                bounds = bounds.add(-width, 0);
                intersects = self.intersectsBounds(bounds, {inclusive: options.inclusive});
            } else if (boundsCrosses && !selfCrosses) {
                self = self.add(-width, 0);
                intersects = bounds.intersectsBounds(self, {inclusive: options.inclusive});
            }
        }
        return intersects;
    }

    /**
     * @function Bounds.prototype.containsBounds
     * @description 
     * @example
     * var bounds = new Bounds(-180,-90,100,80);
     * var isContains = bounds.containsBounds(
     *      new Bounds(-170,-90,100,80),true,true
     *  );
     * @param {Bounds} bounds - 
     * @param {boolean} [partial=false] - 
     *  false
     * @param {boolean} [inclusive=true] - 
     * @returns {boolean} 
     */
    containsBounds(bounds, partial, inclusive) {
        if (partial == null) {
            partial = false;
        }
        if (inclusive == null) {
            inclusive = true;
        }
        var bottomLeft = this.contains(bounds.left, bounds.bottom, inclusive);
        var bottomRight = this.contains(bounds.right, bounds.bottom, inclusive);
        var topLeft = this.contains(bounds.left, bounds.top, inclusive);
        var topRight = this.contains(bounds.right, bounds.top, inclusive);

        return (partial) ? (bottomLeft || bottomRight || topLeft || topRight)
            : (bottomLeft && bottomRight && topLeft && topRight);
    }

    /**
     * @function Bounds.prototype.determineQuadrant
     * @description  bounds  bounds 
     * @example
     * var bounds = new Bounds(-180,-90,100,80);
     * //str = "tr";
     * var str = bounds.determineQuadrant(
     *      new LonLat(20,20)
     *  );
     * @param {LonLat} lonlat - 
     * @returns {string} "br" "tr" "tl" "bl" """""" ""
     */
    determineQuadrant(lonlat) {

        var quadrant = "";
        var center = this.getCenterLonLat();

        quadrant += (lonlat.lat < center.lat) ? "b" : "t";
        quadrant += (lonlat.lon < center.lon) ? "l" : "r";

        return quadrant;
    }

    /**
     * @function Bounds.prototype.wrapDateLine
     * @description  bounds 
     * @example
     * var bounds = new Bounds(380,-40,400,-20);
     * var maxExtent = new Bounds(-180,-90,100,80);
     * //bounds
     * var newBounds = bounds.wrapDateLine(maxExtent);
     * @param {Bounds} maxExtent - 
     * @param {Object} options - 
     * @param {number} [options.leftTolerance=0] - left 
     * @param {number} [options.rightTolerance=0] - right 
     * @returns {Bounds} 
     *                            bounds
     *                            bounds
     */
    wrapDateLine(maxExtent, options) {
        options = options || {};

        var leftTolerance = options.leftTolerance || 0;
        var rightTolerance = options.rightTolerance || 0;

        var newBounds = this.clone();

        if (maxExtent) {
            var width = maxExtent.getWidth();
            // newBounds  maxExtent width
            //shift right?
            while (newBounds.left < maxExtent.left &&
            newBounds.right - rightTolerance <= maxExtent.left) {
                newBounds = newBounds.add(width, 0);
            }
            // newBounds  maxExtent width
            //shift left?
            while (newBounds.left + leftTolerance >= maxExtent.right &&
            newBounds.right > maxExtent.right) {
                newBounds = newBounds.add(-width, 0);
            }
            //
            // crosses right only? force left
            var newLeft = newBounds.left + leftTolerance;
            if (newLeft < maxExtent.right && newLeft > maxExtent.left &&
                newBounds.right - rightTolerance > maxExtent.right) {
                newBounds = newBounds.add(-width, 0);
            }
        }

        return newBounds;
    }

    /**
     * @function Bounds.prototype.toServerJSONObject
     * @description  JSON 
     * @example
     * var bounds = new Bounds(-180,-90,100,80);
     * var obj = bounds.toServerJSONObject();
     * @returns {Object} JSON  Object 
     */
    toServerJSONObject() {
        var jsonObject = {
            rightTop: {x: this.right, y: this.top},
            leftBottom: {x: this.left, y: this.bottom},
            left: this.left,
            right: this.right,
            top: this.top,
            bottom: this.bottom
        }
        return jsonObject;
    }

    /**
     *
     * @function Bounds.prototype.destroy
     * @description 
     *  null
     * @example
     * var bounds = new Bounds(-180,-90,100,80);
     * bounds.destroy();
     */
    destroy() {
        this.left = null;
        this.right = null;
        this.top = null;
        this.bottom = null;
        this.centerLonLat = null;
    }

    /**
     * @function Bounds.fromString
     * @description  bounds 
     * @example
     * var bounds = Bounds.fromString("-180,-90,100,80");
     * @param {string} str - e.g. <i>"5,42,10,45"</i>
     * @param {boolean} [reverseAxisOrder=false] - 
     * truebottom,left,top,rightleft,bottom,right,top
     * @returns {Bounds} 
     */
    static fromString(str, reverseAxisOrder) {
        var bounds = str.split(",");
        return Bounds.fromArray(bounds, reverseAxisOrder);
    }

    /**
     * @function Bounds.fromArray
     * @description  Bounds
     * @example
     * var bounds = Bounds.fromArray([-180,-90,100,80]);
     * @param {Array.<number>} bbox - e.g. <i>[5,42,10,45]</i>
     * @param {boolean} [reverseAxisOrder=false] - truebottom,left,top,rightleft,bottom,right,top
     * @returns {Bounds} 
     */
    static fromArray(bbox, reverseAxisOrder) {
        return reverseAxisOrder === true ?
            new Bounds(bbox[1], bbox[0], bbox[3], bbox[2]) :
            new Bounds(bbox[0], bbox[1], bbox[2], bbox[3]);
    }

    /**
     * @function Bounds.fromSize
     * @description 
     * @example
     * var bounds = Bounds.fromSize(new Size(20,10));
     * @param {Size} size - 
     * @returns {Bounds} 
     */
    static fromSize(size) {
        return new Bounds(0,
            size.h,
            size.w,
            0);
    }

    /**
     * @function Bounds.oppositeQuadrant
     * @description "t""b" "r""l", "tl""br"
     * @param {string} quadrant - "tl"
     * @returns {string} 
     */
    static oppositeQuadrant(quadrant) {
        var opp = "";

        opp += (quadrant.charAt(0) === 't') ? 'b' : 't';
        opp += (quadrant.charAt(1) === 'l') ? 'r' : 'l';

        return opp;
    }

}
