/**
 * @class Credential
 * @deprecatedclass SuperMap.Credential
 * @category Security
 * @classdesc SuperMap  token </br>
 * "http://localhost:8090/iserver/services/security/tokens"  value</br>
 * "2OMwGmcNlrP2ixqv1Mk4BuQMybOGfLOrljruX6VcYMDQKc58Sl9nMHsqQaqeBx44jRvKSjkmpZKK1L596y7skQ.."  value</br>
 * </br>
 * @param {string} value - 
 * @param {string} [name='token'] - name=value  name 
 * @example
 * var pixcel = new Credential("valueString","token");
 * pixcel.destroy();
 * @usage
 */
export class Credential {

    constructor(value, name) {

        /**
         * @member {string} Credential.prototype.value
         * @description 
         */
        this.value = value ? value : "";

        /**
         * @member {string} [Credential.prototype.name='token']
         * @description name=value  name 
         */
        this.name = name ? name : "token";
        this.CLASS_NAME = "SuperMap.Credential";
    }

    /**
     * @function Credential.prototype.getUrlParameters
     * @description  name=value 
     * @example
     * var credential = new Credential("valueString","token");
     * // str = "token=valueString";
     * var str = credential.getUrlParameters();
     * @returns {string}  url 
     */
    getUrlParameters() {
        //return this.name + "=" + this.value + "&" + "...";
        return this.name + "=" + this.value;
    }


    /**
     * @function Credential.prototype.getValue
     * @description  value
     * @example
     * var credential = new Credential("2OMwGmcNlrP2ixqv1Mk4BuQMybOGfLOrljruX6VcYMDQKc58Sl9nMHsqQaqeBx44jRvKSjkmpZKK1L596y7skQ..","token");
     * // str = "2OMwGmcNlrP2ixqv1Mk4BuQMybOGfLOrljruX6VcYMDQKc58Sl9nMHsqQaqeBx44jRvKSjkmpZKK1L596y7skQ..";
     * var str = credential.getValue();
     * @returns {string} value  iServer  value  token 
     */
    getValue() {
        return this.value;
    }

    /**
     *
     * @function Credential.prototype.destroy
     * @description  null
     * @example
     * var credential = new Credential("valueString","token");
     * credential.destroy();
     */
    destroy() {
        this.value = null;
        this.name = null;
    }

}

/**
 * @member {Credential} Credential.CREDENTIAL
 * @description 
 * @example
 * :
 *  // iServervaluename
 *  var value = "(iServertoken)";
 *  var name = "token";
 *  // nametokeniServer
 *  Credential.CREDENTIAL = new Credential(value, name);
 *
 */

 Credential.CREDENTIAL = null;
