/**
 * @function inherit
 * @description  C  P C
 * @param {Object} C - 
 * @param {Object} P - 
 * @private
 */
export var inheritExt = function (C, P) {
    var F = function () {
    };
    F.prototype = P.prototype;
    C.prototype = new F;
    var i, l, o;
    for (i = 2, l = arguments.length; i < l; i++) {
        o = arguments[i];
        if (typeof o === "function") {
            o = o.prototype;
        }
        Util.extend(C.prototype, o);
    }
};


/**
 * @function mixinExt
 * @description 
 * @param {Class|Object} ...mixins - 
 * @private
 */
export var mixinExt = function (...mixins) {

    class Mix {
        constructor(options) {
            for (var index = 0; index < mixins.length; index++) {
                copyProperties(this, new mixins[index](options));
            }
        }
    }

    for (var index = 0; index < mixins.length; index++) {
        var mixin = mixins[index];
        copyProperties(Mix, mixin);
        copyProperties(Mix.prototype, mixin.prototype);
        copyProperties(Mix.prototype, new mixin());
    }
    return Mix;

    function copyProperties(target, source) {
        var ownKeys = Object.getOwnPropertyNames(source);
        if (Object.getOwnPropertySymbols) {
            ownKeys = ownKeys.concat(Object.getOwnPropertySymbols(source));
        }
        for (var index = 0; index < ownKeys.length; index++) {
            var key = ownKeys[index];
            if (key !== "constructor"
                && key !== "prototype"
                && key !== "name" && key !== "length") {
                let desc = Object.getOwnPropertyDescriptor(source, key);
                if (window["ActiveXObject"]) {
                    Object.defineProperty(target, key, desc || {});
                } else {
                    Object.defineProperty(target, key, desc);
                }
            }
        }
    }
};

/**
 * @name String
 * @namespace
 * @category BaseTypes Util
 * @description 
 * @private
 */
export var StringExt = {

    /**
     * @function StringExt.startsWith
     * @description 
     * @param {string} str - 
     * @param {string} sub - 
     * @returns {boolean}  true false
     */
    startsWith: function (str, sub) {
        return (str.indexOf(sub) == 0);
    },

    /**
     * @function StringExt.contains
     * @description 
     * @param {string} str - 
     * @param {string} sub - 
     * @returns {boolean}  true false
     */
    contains: function (str, sub) {
        return (str.indexOf(sub) != -1);
    },

    /**
     * @function StringExt.trim
     * @description 
     * @param {string} str - 
     * @returns {string} 
     */
    trim: function (str) {
        return str.replace(/^\s\s*/, '').replace(/\s\s*$/, '');
    },

    /**
     * @function StringExt.camelize
     * @description ("-")
     * "chicken-head" becomes "chickenHead",
     *       "-chicken-head" becomes "ChickenHead"
     * @param {string} str - 
     * @returns {string}
     */
    camelize: function (str) {
        var oStringList = str.split('-');
        var camelizedString = oStringList[0];
        for (var i = 1, len = oStringList.length; i < len; i++) {
            var s = oStringList[i];
            camelizedString += s.charAt(0).toUpperCase() + s.substring(1);
        }
        return camelizedString;
    },

    /**
     * @function StringExt.format
     * @description  ${token} ,  context 
     * @example
     * 
     * (code)
     * 1template = "${value,getValue}";
     *         context = {value: {getValue:function(){return Math.max.apply(null,argument);}}};
     *         args = [2,23,12,36,21];
     *       :36
     * (end)
     * :
     * (code)
     * 2template = "$${{value,getValue}}";
     *         context = {value: {getValue:function(){return Math.max.apply(null,argument);}}};
     *         args = [2,23,12,36,21];
     *       :"${36}"
     * (end)
     * :
     * (code)
     * 3template = "${a,b}";
     *         context = {a: {b:"format"}};
     *         args = null;
     *       :"format"
     * (end)
     * :
     * (code)
     * 3template = "${a,b}";
     *         context = null;
     *         args = null;
     *       :"${a.b}"
     * (end)
     * @param {string} template -  template "${token}" token  context["token"] 
     * @param {Object} [context=window] -  window 
     * @param {Array.<number>} [args] -  context 
     * @returns {string}  context 
     */
    format: function (template, context, args) {
        if (!context) {
            context = window;
        }

        // Example matching:
        // str   = ${foo.bar}
        // match = foo.bar
        var replacer = function (str, match) {
            var replacement;

            // Loop through all subs. Example: ${a.b.c}
            // 0 -> replacement = context[a];
            // 1 -> replacement = context[a][b];
            // 2 -> replacement = context[a][b][c];
            var subs = match.split(/\.+/);
            for (var i = 0; i < subs.length; i++) {
                if (i == 0) {
                    replacement = context;
                }

                replacement = replacement[subs[i]];
            }

            if (typeof replacement === "function") {
                replacement = args ?
                    replacement.apply(null, args) :
                    replacement();
            }

            // If replacement is undefined, return the string 'undefined'.
            // This is a workaround for a bugs in browsers not properly
            // dealing with non-participating groups in regular expressions:
            // http://blog.stevenlevithan.com/archives/npcg-javascript
            if (typeof replacement == 'undefined') {
                return 'undefined';
            } else {
                return replacement;
            }
        };

        return template.replace(StringExt.tokenRegEx, replacer);
    },

    /**
     * @member {RegExp} [StringExt.tokenRegEx]
     * @description  token  tokenRegEx=/\$\{([\w.]+?)\}/g
     * @example
     * Examples: ${a}, ${a.b.c}, ${a-b}, ${5}
     */
    tokenRegEx: /\$\{([\w.]+?)\}/g,

    /**
     * @member {RegExp} [StringExt.numberRegEx]
     * @description  numberRegEx=/^([+-]?)(?=\d|\.\d)\d*(\.\d*)?([Ee]([+-]?\d+))?$/
     */
    numberRegEx: /^([+-]?)(?=\d|\.\d)\d*(\.\d*)?([Ee]([+-]?\d+))?$/,

    /**
     * @function StringExt.isNumeric
     * @description 
     * @example
     * (code)
     * StringExt.isNumeric("6.02e23") // true
     * StringExt.isNumeric("12 dozen") // false
     * StringExt.isNumeric("4") // true
     * StringExt.isNumeric(" 4 ") // false
     * (end)
     * @returns {boolean}  true false
     */
    isNumeric: function (value) {
        return StringExt.numberRegEx.test(value);
    },

    /**
     * @function StringExt.numericIf
     * @description 
     * @returns {(number|string)} 
     */
    numericIf: function (value) {
        return StringExt.isNumeric(value) ? parseFloat(value) : value;
    }

};

/**
 * @name Number
 * @namespace
 * @category BaseTypes Util
 * @description 
 * @private
 */
export var NumberExt = {

    /**
     * @member {string} [NumberExt.decimalSeparator='.']
     * @description 
     * @constant
     */
    decimalSeparator: ".",

    /**
     * @member {string} [NumberExt.thousandsSeparator=',']
     * @description 
     * @constant
     */
    thousandsSeparator: ",",

    /**
     * @function NumberExt.limitSigDigs
     * @description 
     * @param {number} num - 
     * @param {number} sig - 
     * @returns {number} 
     */
    limitSigDigs: function (num, sig) {
        var fig = 0;
        if (sig > 0) {
            fig = parseFloat(num.toPrecision(sig));
        }
        return fig;
    },

    /**
     * @function NumberExt.format
     * @description 
     * @param {number} num - 
     * @param {number} [dec=0]  -  null 
     * @param {string} [tsep=','] - 
     * @param {string} [dsep='.'] - 
     * @returns {string} 
     */
    format: function (num, dec, tsep, dsep) {
        dec = (typeof dec != "undefined") ? dec : 0;
        tsep = (typeof tsep != "undefined") ? tsep :
        NumberExt.thousandsSeparator;
        dsep = (typeof dsep != "undefined") ? dsep :
        NumberExt.decimalSeparator;

        if (dec != null) {
            num = parseFloat(num.toFixed(dec));
        }

        var parts = num.toString().split(".");
        if (parts.length === 1 && dec == null) {
            // integer where we do not want to touch the decimals
            dec = 0;
        }

        var integer = parts[0];
        if (tsep) {
            var thousands = /(-?[0-9]+)([0-9]{3})/;
            while (thousands.test(integer)) {
                integer = integer.replace(thousands, "$1" + tsep + "$2");
            }
        }

        var str;
        if (dec == 0) {
            str = integer;
        } else {
            var rem = parts.length > 1 ? parts[1] : "0";
            if (dec != null) {
                rem = rem + new Array(dec - rem.length + 1).join("0");
            }
            str = integer + dsep + rem;
        }
        return str;
    }
};

if (!Number.prototype.limitSigDigs) {
    /**
     * APIMethod: Number.limitSigDigs
     * .
     * @param {number} sig -
     * @returns {number} 
     *             null0,  0
     */
    Number.prototype.limitSigDigs = function (sig) {
        return NumberExt.limitSigDigs(this, sig);
    };
}

/**
 * @name Function
 * @namespace
 * @category BaseTypes Util
 * @description 
 * @private
 */
export var FunctionExt = {
    /**
     * @function FunctionExt.bind
     * @description  this 
     * @param {function} func - 
     * @param {Object} object -  this 
     * @returns {function} object  func  this 
     */
    bind: function (func, object) {
        // create a reference to all arguments past the second one
        var args = Array.prototype.slice.apply(arguments, [2]);
        return function () {
            // Push on any additional arguments from the actual function call.
            // These will come after those sent to the bind call.
            var newArgs = args.concat(
                Array.prototype.slice.apply(arguments, [0])
            );
            return func.apply(object, newArgs);
        };
    },

    /**
     * @function FunctionExt.bindAsEventListener
     * @description 
     * @param {function} func - 
     * @param {Object} object - this 
     * @returns {function}
     */
    bindAsEventListener: function (func, object) {
        return function (event) {
            return func.call(object, event || window.event);
        };
    },

    /**
     * @function FunctionExt.False
     * @description  false IE8  DOM 
     * @example
     * document.onclick = FunctionExt.False;
     * @returns {boolean}
     */
    False: function () {
        return false;
    },

    /**
     * @function FunctionExt.True
     * @description  true IE8  DOM 
     * @example
     * document.onclick = FunctionExt.True;
     * @returns {boolean}
     */
    True: function () {
        return true;
    },

    /**
     * @function FunctionExt.Void
     * @description  "undefined"
     * @returns {undefined}
     */
    Void: function () {
    }

};

/**
 * @name Array
 * @namespace
 * @category BaseTypes Util
 * @description 
 * @private
 */
export var ArrayExt = {

    /**
     * @function ArrayExt.filter
     * @description  ECMA-262  Array.prototype.filter {@link http://developer.mozilla.org/en/Core_JavaScript_1.5_Reference/Global_Objects/Array/filter}
     * @param {Array} array - 
     * @param {function} callback - </br>
     *      true: </br>
     *      caller callback  caller  callback </br>
     * @param {Object} [caller] -  callback  caller  callback 
     * @returns {Array} callback  true 
     */
    filter: function (array, callback, caller) {
        var selected = [];
        if (Array.prototype.filter) {
            selected = array.filter(callback, caller);
        } else {
            var len = array.length;
            if (typeof callback != "function") {
                throw new TypeError();
            }
            for (var i = 0; i < len; i++) {
                if (i in array) {
                    var val = array[i];
                    if (callback.call(caller, val, i, array)) {
                        selected.push(val);
                    }
                }
            }
        }
        return selected;
    }

};
